.footer {
    transform: translateX(20em);
    text-align: center;
    margin-top: 5em;
    padding-top: 1em;
    padding-bottom: 1em;
    transition: all 1s;
    position: relative;
    width: 40%;
    height: 100%;
    border: 1px solid var(--main-color);
    background: rgba($color: #38224d, $alpha: 0.9);
    &__development {
        font-family: uaBRAND;
        color: var(--second-color);
        a {
            color: var(--white-color);
        }
    }
    &__links {
        margin-top: 1em;
        img {
            margin-right: 1em;
            width: 3em;
            &:hover {
                transition: all .5s;
                transform: scale(1.2);
            }
        }
    }
}